/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  --clr-primary-5: hsl(21, 62%, 45%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}
/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  line-height: 1.5;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
/* h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
} */
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  color: var(--clr-grey-5);
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
/*  global classes */

/* section */
.section {
  width: 90vw;
  margin: 5rem auto;
  max-width: var(--max-width);
}

@media screen and (min-width: 992px) {
  .section {
    width: 95vw;
  }
}
/*
=============== 
Slider
===============
*/
/* .title {
  text-align: center;
  margin-bottom: 2rem;
} */
.title h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: normal;
  font-weight: 300;
}
.title span {
  font-size: 0.85em;
  font-weight: 700;
  margin-right: 1rem;
  color: var(--clr-primary-5);
}
.section-center {
  position: relative;
  width: 46vw;
  height: 390px;
  max-width: 800px;
  text-align: center;
  overflow: hidden;
  display: flex;
  border-radius: 10px;
  background: rgb(215, 215, 215);
  margin: 0 auto;
  margin-top: 4rem;
}
.person-img {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border: 4px solid var(--clr-grey-8);
  box-shadow: var(--dark-shadow);
  border-radius: 50%;
  margin-bottom: 1rem;
  margin: 1rem;
}
article h4 {
  text-transform: uppercase;

  margin-bottom: 0.25rem;
  color: var(--clr-primary-5);
}
.title {
  text-transform: capitalize;
  margin-bottom: 3.75rem;
  color: hsl(21, 62%, 45%);
}
.text {
  max-width: 30em;

  line-height: 2;
  color: var(--clr-grey-5);
  margin: 0 auto;
  margin-top: 2rem;
}
.icon {
  font-size: 3rem;
  margin-top: 1rem;
  color: var(--clr-primary-5);
}
.prev,
.next {
  position: absolute;
  top: 200px;
  width: 1.25rem;
  height: 1.25rem;
  display: grid;
  place-items: center;
  border-color: transparent;
  font-size: 1rem;
  border-radius: var(--radius);
  cursor: pointer;
  transition: var(--transition);
  transform: translateY(-50%);
  margin: 1rem;
  background: var(--clr-grey-5);
  color: var(--clr-white);
}
.prev:hover,
.next:hover {
  background: var(--clr-primary-5);
}
.prev {
  left: 0;
}
.next {
  right: 0;
}
@media (min-width: 800px) {
  .text {
    max-width: 30em;
  }
  .prev,
  .next {
    font-size: 1.5rem;
    width: 2rem;
    height: 2rem;
  }
}
article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: var(--transition);
}
article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}
article.lastSlide {
  transform: translateX(-100%);
}
article.nextSlide {
  transform: translateX(100%);
}
@media screen and (max-width: 768px) {
  .section-center {
    width: 60vw;
    height: 390px;
  }
  .text {
    max-width: 26em;
  }
}
@media screen and (max-width: 420px) {
  .section-center {
    width: 76vw;
    height: 390px;
  }
  .title h2 {
    font-weight: 500;
    font-size: 25px;
  }
}
