.whatGpt_container {
  display: flex;
  flex-direction: column;

  background: #f5ebd8;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  padding: 3rem;
}

.whatGpt_container-content {
  display: flex;
}

.whatGpt_container-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 3rem 0 3rem;
}

.whatGpt_container-heading h1 {
  font-weight: 800;
  font-size: 25px;
  line-height: 45px;
  max-width: 650px;
  font-family: "Manrope";
  font-style: normal;
}
.whatGpt_container-heading p {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  font-family: "Manrope";
  color: var(--color-subtext);
}
.whatGpt_container-gradient-text {
  background-clip: text;
  background: var(--gradient-text);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.whatGpt_container-3feachers {
  display: flex;
}

@media screen and (max-width: 820px) {
  .whatGpt_container {
    padding: 1rem;
  }
}
@media screen and (max-width: 768px) {
  .whatGpt_container {
    padding: 1rem;
  }
  .whatGpt_container-3feachers {
    display: flex;
    flex-direction: column;
  }
  .whatGpt_container-heading h1 {
    font-size: 24px;
    line-height: 35px;
    max-width: 625px;
  }
  .whatGpt_container-heading {
    display: flex;
    flex-direction: column;
  }
}
@media screen and (max-width: 420px) {
  .whatGpt_container {
    /* margin: 1rem 1rem; */
    padding: 1rem;
  }
}
