.dse_navbar-above {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(43 53 124);
  /* background-color: rgb(221, 221, 221); */
}
.dse-navbar-email {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dse-navbar-email a,
p {
  color: white;
  padding: 0.3rem;
}
.dse-navbar-icons a {
  font-size: 25px;
  color: white;
  padding: 1.3rem;
  /* color: #3e406a; */
}
.dse-navbar-icons a svg:hover {
  /* background: rgb(143, 143, 143); */

  color: #ff4820;
  border-radius: 6px;
}

.dse_navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: #b5b5b5;
  padding: 0.5rem 6rem;
  /* background-color: #042c54; */
}
.dse_navbar-links {
  display: flex;
  flex: 1;
  justify-content: flex-start;
  align-items: center;
}

.dse_navbar p:hover {
  border-bottom: 1px solid white;
}
.gpt_navbar-links_logo {
  cursor: pointer;
  margin-right: 2rem;
}
.gpt_navbar-links_logo img {
  width: 100%;
  height: 2rem;
}
.dse_navbar-links_container {
  display: flex;
}
.dse_navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.dse_navbar-links_container p,
.dse_navbar-sign p,
.dse_navbar-menu_container p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 15px;
  line-height: 25px;
  text-transform: none;
  border: none;
  margin: 0 1rem;
  cursor: pointer;
  color: white;
}

/* side Menu */
.dse_navbar-menu_container button,
.dse_navbar-sign button {
  font-weight: 500;
  font-family: var(--font-family);
  border-radius: 5px;
  font-size: 15px;
  line-height: 25px;
  outline: none;
  cursor: pointer;
  padding: 0.3rem 1rem;
  color: white;
  background: #ff4820;
  border: navajowhite;
}
.dse_navbar-menu_container button,
.dse_navbar-sign button:hover {
  transform: scale(1.1);
  transition: 0.5s;
  background: #246c34;
}
.dse_navbar-menu {
  display: none;
  margin-left: 1rem;
  position: relative;
}
.dse_navbar-menu svg {
  cursor: pointer;
}
.dse_navbar-menu_container {
  top: 40px;
  right: 0;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: end;
  padding: 2rem;
  position: absolute;
  min-width: 210px;
  margin-top: 1rem;
  border-radius: 5px;
  background: var(--color-footer);
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.dse_navbar-menu_container p {
  margin: 1rem 0;
}
.dse_navbar-menu_container-links-sign {
  display: none;
}

.navbar_menu-buttom-section {
  /* font-size: 25px; */
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  background-color: rgb(225 225 225);
}
.navbar_menu-buttom-section img {
  width: 4rem;
  padding: 2px;
}
.navbar_menu-buttom-section h3 {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 22px;
  font-weight: 00;
  letter-spacing: 5px;
  line-height: 3;
  text-transform: capitalize;
  margin: 0;
  padding-left: 2rem;
}
.navbar_menu-buttom-section h2 span {
  font-weight: 700;

  letter-spacing: 5px;
  color: #cc9c3a;
}

@media screen and (max-width: 1050px) {
  .dse_navbar-links_container {
    display: none;
  }
  .dse_navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 720px) {
  .navbar_menu-buttom-section {
    justify-content: space-between;
  }
  .navbar_menu-buttom-section h3 {
    font-size: 18px;
  }
}

@media screen and (max-width: 700px) {
  .dse_navbar {
    padding: 2rem 4rem;
  }
}
@media screen and (max-width: 550px) {
  .dse_navbar {
    padding: 2rem;
  }
  .dse_navbar-sign {
    display: none;
  }

  .dse_navbar-menu_container-links-sign {
    display: block;
  }
  .dse-navbar-email a,
  p {
    padding: 0.3rem;
    color: #777181;
    font-size: 10px;
  }
  .dse-navbar-icons a {
    padding: 0.7rem;
    font-size: 15px;
  }
  .navbar_menu-buttom-section h2 {
    display: none;
  }
  .navbar_menu-buttom-section {
    justify-content: space-evenly;
  }
  .navbar_menu-buttom-section img {
    width: 3.55rem;
  }
}
