.about_section {
  padding: 2rem;
}
.about_section h2 {
  font-size: 30px;
  font-weight: 600;
  font-style: normal;
}

.h2 {
  text-align: center;
}
.about_section-block {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  margin-top: 4rem;
}
.paragraph {
  width: 37rem;
}

.uni-img {
  width: 35%;
  /* padding: 3rem; */
}

@media screen and (max-width: 800px) {
  .about_section-block {
    flex-direction: column;
  }
  .paragraph {
    width: auto;
  }
  .uni-img {
    width: 50%;
    /* padding: 3rem; */
  }
}
@media screen and (max-width: 400px) {
  .uni-img {
    width: 95%;
    /* padding: 3rem; */
  }
}
