.header_container {
  display: flex;
  justify-content: space-around;
  padding-top: 2rem;
  background-color: #c0c8db;
  box-shadow: 5px 10px #888888;
}
.header_container-content {
  display: flex;
  flex-direction: column;
  margin-left: 5rem;
  flex: 1;
  justify-content: center;
} /*  */
.p {
  position: relative;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 26px;
  font-weight: 600;
  letter-spacing: 4px;
  overflow: hidden;
  background: linear-gradient(90deg, #000, #000000, #000);
  background-repeat: no-repeat;
  background-size: 80%;

  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0);
}
/*  */

.header_container-content h1 {
  font-weight: 800;
  font-family: "Manrope";
  line-height: 74px;
  font-size: 57px;
  color: white;
}
.header_container-content h1 span {
  font-size: 57px;
}
.header_container-content p {
  font-family: "Manrope";
  font-size: 22px;
  font-weight: 400;
  padding: 1.5rem 0;
  line-height: 27px;
  color: var(--color-text);
}
.header_container-input input {
  padding: 18px;
  width: 22rem;
  background: #052d56;
  border-radius: 5px 0px 0px 5px;
  border: none;
}
.header_container-input button {
  padding: 18px;
  border: none;
  font-weight: 700;
  background: #ff4820;
  color: white;
  cursor: pointer;
}
.header_container-input button:hover {
  background: #246c34;
  transform: scale(1.1);
  transition: 0.5s;
}
.header_container-people {
  display: flex;
  align-items: center;
}
.header_container-people img {
  width: 12rem;
  height: 2rem;
}

.header_container-people p {
  font-size: 12px;
  font-weight: 400;
  color: white;
  line-height: 36px;
}
.header_container-img {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header_container-img img {
  width: 100%;
  height: 100%;
}
.gradient-text {
  --gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
}

@media screen and (max-width: 1050px) {
  .header_container {
    flex-direction: column;
  }
  .header_container-content {
    margin: 1rem 2rem;
  }
}
@media screen and (max-width: 650px) {
  .header_container-content h1 {
    font-size: 40px;
    line-height: 40px;
  }
  .header_container-content p {
    font-size: 15px;
  }
  .header_container-people {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }
  .para {
    padding: 0;
  }
  .header_container-input input {
    padding: 15px;
    width: 15rem;
  }
  .header_container-input button {
    padding: 15px;
    border-radius: 5px;
  }
  .uni_name {
    line-height: normal;
  }
}
@media screen and (max-width: 420px) {
  .p {
    font-size: 18px;
    font-weight: 500;
  }

  .header_container-content h1 {
    font-size: 25px;
    line-height: 45px;
  }
  .header_container-content p {
    font-size: 12px;
  }

  .header_container-people {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }
  .para {
    padding: 0;
  }
  .header_container-input input {
    padding: 12px;
    width: 12rem;
  }
  .header_container-input button {
    padding: 12px;
  }
}
