.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 356px;
  margin: auto 1rem;
  text-align: start;
  font-family: arial;
  /* background-color: #021324; */
  background-color: #fff;
}
.card:hover {
  background-color: #c0c8db;
  transition: 2s;
}
.image {
  width: 100%;
  height: 21em;
  object-fit: cover;
  opacity: 100%;
}
.image:hover {
  opacity: 60%;
}
.card-subSection {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card-subSection h5 {
  color: hsl(21, 62%, 45%);
}
.card-subSection h3 {
  color: #042c54;
  font-size: 26px;
  font-weight: 500;
  font-family: "Manrope";
  line-height: 1.3;
  letter-spacing: 1px;
}
